.formFields
{
    background-color: #f3f3f3;
    padding: 40px 100px;
    width: 600px;
    margin: 40px auto;
    border-radius: 8px;
    max-width: 100%;
}
.formFields label{
    margin-top: 12px;
    display: inline-block;
    margin-bottom: 4px;
}
.formFields input
{
    width: 100%;
    height: 38px;
    border-radius: 4px;
    border:1px solid #4a4a4a;
    padding-left: 10px;
    background-color: #fff;
}
.formFields select
{
    width: 100%;
    height: 38px;
    border-radius: 4px;
    border:1px solid #4a4a4a;
    background-color: #fff;
    padding-left: 10px;
}
.formFields select option
{

}
.formFields textarea
{
    width: 100%;
    height: 96px;
    border-radius: 4px;
    border:1px solid #4a4a4a;
    padding-left: 10px;
}
.formFields button
{
    width: 100%;
    height: 38px;
    margin-top: 16px;
    border: 0;
    background-color: #1a6284;
    color: #fff;
    border-radius: 4px;
    cursor: pointer;
    border: 1px solid #105270;
}
input, select, button, option, textarea, button{
    outline: none;
}
.text-center
{
    text-align: center;
}